body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.colors-main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: black;
  height: 98vh;
  padding: 0 10px 0 10px;
  border-right: 1px solid white; }

.peg,
.colors-main button {
  width: 9vh;
  height: 9vh;
  margin: 2px;
  border-radius: 50%; }

.peg:hover {
  cursor: pointer; }

.w-peg {
  background-image: radial-gradient(#ebebeb, white 55%, #333333); }

.r-peg {
  background-image: radial-gradient(#d60000, red 55%, black); }

.o-peg {
  background-image: radial-gradient(#d68b00, orange 55%, black); }

.y-peg {
  background-image: radial-gradient(#d6d600, yellow 55%, black); }

.gn-peg {
  background-image: radial-gradient(#005700, green 55%, black); }

.b-peg {
  background-image: radial-gradient(#0000d6, blue 55%, black); }

.p-peg {
  background-image: radial-gradient(#570057, purple 55%, black); }

.g-peg {
  background-image: radial-gradient(#6c6c6c, gray 55%, black); }

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.peg-board {
  display: -webkit-flex;
  display: flex; }

.grid {
  display: grid;
  grid-template-columns: repeat(30, 3.4%);
  width: 98vh;
  height: 98vh; }

.btn-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: black;
  border-left: 1px solid white;
  padding: 0 10px 0 10px; }

.btn-box button {
  width: 9vh;
  height: 9vh;
  margin: 2px;
  border-radius: 50%;
  color: red;
  cursor: pointer; }

button:focus {
  outline: 0; }

.lightOn {
  background-image: radial-gradient(#d6d600, yellow 55%, black);
  border: none; }

.lightOff {
  background-image: radial-gradient(#d68b00, orange 55%, black);
  border: none; }

.reset {
  background-color: black;
  border: 1px solid white;
  font-size: 1.8vh; }

.reset:hover {
  background-color: white;
  border: 1px solid black; }

#g {
  background-color: #161616; }

#w {
  background-image: radial-gradient(lightgray 45%, #545454); }

#r {
  background-image: radial-gradient(red 45%, black); }

#o {
  background-image: radial-gradient(orange 45%, black); }

#y {
  background-image: radial-gradient(yellow 45%, black); }

#gn {
  background-image: radial-gradient(green 45%, black); }

#b {
  background-image: radial-gradient(blue 45%, black); }

#p {
  background-image: radial-gradient(purple 45%, black); }

.hole-border {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: black; }

.hole-border div {
  border-radius: 50%;
  width: 70%;
  height: 70%;
  margin: 0; }

#w.on {
  background-color: white;
  box-shadow: 0px 0px 20px white;
  background-image: none; }

#r.on {
  background-color: #ff3333;
  box-shadow: 0px 0px 20px #ff6666;
  background-image: none; }

#o.on {
  background-color: #ffb733;
  box-shadow: 0px 0px 20px #ffc966;
  background-image: none; }

#y.on {
  background-color: #ffff33;
  box-shadow: 0px 0px 20px #ffff66;
  background-image: none; }

#gn.on {
  background-color: #00b300;
  box-shadow: 0px 0px 20px #00e600;
  background-image: none; }

#b.on {
  background-color: #3333ff;
  box-shadow: 0px 0px 20px #6666ff;
  background-image: none; }

#p.on {
  background-color: #b300b3;
  box-shadow: 0px 0px 20px #e600e6;
  background-image: none; }

